import Vue from 'vue'
import Vuex from 'vuex'
import request from "@/request";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: "",
    user: {
      id:0,
      account: '',
      balance: 0,
      deal: 0, // 处理量
      yesterday: 0, // 昨日处理量
      role: 2 // 1表示录单员2表示接单
    }
  },
  mutations: {
    // @ts-ignore
    updateToken: function (state, payload) {
      state.token = payload
    },
    // @ts-ignore
    updateUserInfo: function (state, payload) {
      state.user = {...state.user, ...payload}
    },
  },
  actions: {
    // @ts-ignore
    login: function ({ commit }, payload) {
      return request.Auth.login(payload).then(res=>{
        if (parseInt(res.status) == 1) {
          commit('updateToken', res.data.sid);
          localStorage.setItem('token', res.data.grant);
        }
        return res;
      });
    },
    // @ts-ignore
    logout: function ({ commit }) {
      commit('updateToken', '');
      request.Auth.logout(localStorage.getItem('token') as string);
      localStorage.removeItem('token');
    },
    // @ts-ignore
    getprofile: function ( { commit } ) {
      request.User.profile().then(res=>{
        // @ts-ignore
        if (res.status > 0) {
          // 保存用户信息至store
          // @ts-ignore
          commit('updateUserInfo', res.data);
        }
      });
    },
  },
  modules: {
  }
})
