import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'login',
    component: () => import('@/views/Login.vue'),
  },
  {
    path: '/bd',
    name: 'layout',
    redirect: '/bd/home',
    component: () => import('@/components/Layout.vue'),
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/Home.vue'),
        meta: '/bd/home'
      },
      {
        path: 'statistics',
        name: 'statistics',
        component: () => import('@/views/Statistics.vue'),
        meta: '/bd/statistics'
      },
      {
        path: 'status',
        name: 'status',
        component: () => import('@/views/Status.vue'),
        meta: '/bd/status'
      },
      {
        path: 'list',
        name: 'list',
        component: () => import('@/views/List.vue'),
        meta: '/bd/list'
      },
      {
        path: 'qrgrant',
        name: 'qrgrant',
        component: () => import('@/views/Qrgrant.vue'),
        meta: '/bd/list'
      },
      {
        path: 'qrverify/:id',
        name: 'qrverify',
        component: () => import('@/views/Qrverify.vue'),
        meta: '/bd/list'
      },
    ]
  },
  {
    path: '/verify/:account?',
    name: 'verify',
    component: () => import('@/views/Verify.vue'),
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.fullPath.match(/^\/verify$|^\/verify\/.*/) != null) {
    next();
  }else{
    if (localStorage.getItem('token') == null) {
      if (to.fullPath.match(/^\/bd$|^\/bd\/.*/) != null) {
        next('/');
      }else{
        next();
      }
    }else{
      if (to.fullPath.match(/^\/bd$|^\/bd\/.*/) != null) {
        next();
      }else{
        next('/bd');
      }
    }
  }


});

export default router
