function appPost(path: string, params={}): Promise<object> {
    return axios.post(path, params, {
        headers: {
            SID: store.state.token
        }
    }).then(res=>{
        if (parseInt(res.data.status) == -403 && res.data.msg == 'forbidden') {
            return axios.post(api.base + api.path.auth.refresh, {}, {
                headers: {
                    TOKEN: localStorage.getItem('token')
                }
                // @ts-ignore
            }).then(res => {
                if (res.data.status > 0) {
                    // 成功获取到新的token
                    store.commit('updateToken', res.data.data.sid);
                    localStorage.setItem('token', res.data.data.grant);

                    return appPost(path, params);
                } else {
                    // 重新登录
                    store.dispatch('logout');
                    router.push('/');
                    return new Promise(()=>null); // 终止链式操作
                }
            });
        } else {
            return res.data;
        }
    });
}


class Auth {
    static login(data: object) {
        return post(api.base+api.path.auth.login, data);
    }

    static logout(token: string) {
        return token;
    }
}

class User {
    static profile() {
        return appPost(api.base+api.path.user.profile);
    }

    static statistics() {
        return appPost(api.base+api.path.user.statistics);
    }

    static getbalance(account: string) {
        return appPost(api.base+api.path.user.getbalance, { account });
    }

    static addbalance(account: string, amount: number) {
        return appPost(api.base+api.path.user.addbalance, { account, amount });
    }
}

class Douyin {
    static grant() {
        return appPost(api.base+api.path.douyin.grant);
    }

    static check(token: string, cookie: string) {
        return appPost(api.base+api.path.douyin.check, {token, cookie});
    }

    static status(id: number) {
        return appPost(api.base+api.path.douyin.status, { id });
    }

    // 下面两个是不验权的
    // 获取认证状态，不验权
    static info(account: string) {
        return post(api.base+api.path.douyin.info, { account });
    }
    // 提交身份证信息并获取扫脸链接，不验权
    static submit(data: object) {
        return post(api.base+api.path.douyin.submit, data);
    }
}

class Account {
    static list(page: number, id = 0, account = '') {
        return appPost(api.base+api.path.account.list + '?p='+page+'&id='+id+'&account='+account);
    }

    static info(id:number = 0) {
        return appPost(api.base+api.path.account.info, { id });
    }

    static collect() {
        return appPost(api.base+api.path.account.collect);
    }

    static del(id:number) {
        return appPost(api.base+api.path.account.del, { id });
    }

    static delay(id:number) {
        return appPost(api.base+api.path.account.delay, { id });
    }
}

export default {
    Auth,
    User,
    Douyin,
    Account
};